html,
body {
    margin: 0;
    background: #fff;
	font-family: 'Open Sans', sans-serif;
	overflow-x: hidden;
}

h4,
h5,
h6,
h1,
h2,
h3 {
    margin: 0;
}

p {
    margin: 0;
}

ul,
label {
    margin: 0;
    padding: 0;
}

body a {
    transition: 0.5s all;
    -webkit-transition: 0.5s all;
    -o-transition: 0.5s all;
    -moz-transition: 0.5s all;
    -ms-transition: 0.5s all;
    text-decoration: none;

}

body a:hover {
    text-decoration: none;
}

/*-- footer-top --*/
.footer-top {
    background: #444;
    padding: 20px 0;
    margin-top: 10px;
}

.clearfix::after {
    content: '';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

.clearfix {
    *zoom: 1;
}

.footer-top .container {
    box-sizing: border-box;
}

.footer-top .container .footer-grids {
    width: 20%;
    box-sizing: border-box;
    float: left;
    text-align: center;
}

.footer-top h3 {
    color: #fff;
    font-size: 25px;
    font-family: 'Days One', sans-serif;
    margin-bottom: 30px;
}

.footer-top ul li {
    list-style-type: none;
    line-height: 2.1em;
    font-size: 14px;
    color: #9C9C9C;
}

.footer-grids ul li a {
    color: #9C9C9C;
    text-decoration: none;
}

.footer-grids ul li a:hover,
.social-icons ul li a:hover {
    /* color: #00B5B5; */
}

.social-icons ul li a {
    color: #9C9C9C;
    text-decoration: none;
    padding-left: 28px;
}

.footer-grids h2 a {
    font-size: 24px;
    color: #ff0003;
    text-decoration: none;
    font-family: 'Days One', sans-serif;
}

.footer-top p {
    text-align: center;
    margin-top: 60px;
    color: #fff;
    font-size: 15px;
    flex-wrap: wrap;
}

.footer-top p a {
    color: #fff;
}

.footer-top p a:hover {
    color: #00B5B5;
}

.footer-grids h2 {
    margin-bottom: 5px;
}

.wechat-box {
    position: relative;
}

.wechat-box img.wechat {
    width: 90px;
    height: 90px;
    position: absolute;
    bottom: -88px;
    right: 40px;
}

/*-- //footer-top --*/

/*-- responsive media queries --*/
@media (max-width: 1080px) {
    .footer-top h3 {
        font-size: 22px;
    }

    .footer-top ul li {
        font-size: 13px;
    }

    .footer-grids h2 a {
        font-size: 20px;
    }

    h3.tittle {
        font-size: 32px;
    }
}

@media (max-width: 991px) {

    .footer-grids,
    .social-icons {
        float: left;
        width: 40%;
        padding: 0;
    }

    .footer-top h3 {
        font-size: 21px;
    }

    h3.tittle {
        font-size: 30px;
    }

    .gallery-grids figcaption h4 {
        font-size: 22px;
    }

    .footer-top p {
        margin-top: 40px;
        font-size: 14px;
    }

    .footer-grids h2 a {
        font-size: 17px;
    }

    .footer-top h3 {
        margin-bottom: 23px;
    }
}
	
@media (max-width: 760px) {
    .footer-top .container .footer-grids {
        width: 50%;
    }

    .footer-top .container .footer-grids:nth-child(5) {
        margin-left: 50%;
        transform: translateX(-50%);
    }
}

@media (max-width: 736px) {

    .footer-grids,
    .social-icons {
        float: left;
        width: 50%;
        min-height: 210px;
    }

    .footer-top p {
        margin-top: 18px;
        font-size: 13px;
    }

    .footer-top h3 {
        font-size: 19px;
    }

    .gallery-grids figcaption h4 {
        margin-bottom: 19px;
    }

    .footer-grids,
    .social-icons {
        padding: 0 15px;
    }
}

@media (max-width: 640px) {
    h3.tittle {
        font-size: 28px;
        margin-bottom: 35px;
    }
}

@media (max-width: 600px) {}

@media (max-width: 568px) {
    .gallery-grids figure figcaption {
        padding: .5em;
    }
}

@media screen and (max-width: 540px) {
	.wechat-box img.wechat {
			position: absolute;
			bottom: 50px;
			right: -80px;
		}
}

@media (max-width: 414px) {

    .footer-grids,
    .social-icons {
        float: left;
        width: 100%;
        min-height: inherit;
    }

    .footer-top h3 {
        margin-bottom: 15px;
    }

    .footer-grids,
    .social-icons {
        margin-bottom: 25px;
        margin-top: 0 !important;
    }
}

@media (max-width: 384px) {
    .footer-top p {
        margin-top: 0;
    }

    .gallery-grids figcaption h4 {
        margin-bottom: 10px;
    }
}

