@charset "UTF-8";
body {
    background-color: #FFFFFF;
}

.block {
    display: block;
    width: 100%;
    background-color: #FFFFFF;
}
.bg-title {
    width: 100%;
    background-size: cover;
}

.bg-title img {
    /*//display: block;*/
    width: 100%;
    height: 200px;
    background-size: cover;
}

.wrapper {
    width: 100%;
    display: flex;
    height: 150px;
    justify-content: center;
    margin-top: 10px;
}

.wrapper .main-wrapper {
    width: 1140px;
    display: flex;
}

.wrapper .main-wrapper .left-menu {
    width: 250px;
    margin-right: 35px;
    position: relative;
    top: 0;
}

.wrapper .main-wrapper .left-menu .title {
    width: 100%;
    display: flex;
    align-items: center;
    color: #999999;
    font-size: 14px;
    padding: 4px 0;
    white-space: nowrap;
    border-bottom: 1px solid #E5E5E5;
}

.wrapper .main-wrapper .left-menu .title span.line-icon {
    display: inline-block;
    margin-left: 10px;
    width: 15px;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
}

.wrapper .main-wrapper .left-menu .title span.line-icon span.line {
    display: block;
    height: 2px;
    background-color: #E1E1E1;
}

.wrapper .main-wrapper .right-wrapper .header span.news-title {
    font-size: 22px;
    color: #333333;
}

.wrapper .main-wrapper .right-wrapper .header span.right {
    color: #999999;
    font-size: 14px;
    padding-right: 100px;
    display: block;
    /* flex: 1; */
    text-align: right;
    margin-top: 20px;
}

.wrapper .main-wrapper .right-wrapper .header span.right span.author {
    margin-right: 30px;
}

.wrapper .main-wrapper .right-wrapper ul.keyword-list {
    width: 100%;
    padding: 15px 0 40px;
}
ul.keyword-list {
    width: 100%;
    padding: 15px 0 40px;
}

.wrapper .main-wrapper .right-wrapper ul.keyword-list li.keyword-item {
    display: inline-block;
    padding: 5px 10px;
    color: #F42434;
    font-size: 14px;
    border: 1px solid #FF6D6D;
    border-radius: 2px;
    margin-right: 15px;
}
ul.keyword-list li.keyword-item {
    display: inline-block;
    padding: 5px 10px;
    color: #F42434;
    font-size: 14px;
    border: 1px solid #FF6D6D;
    border-radius: 2px;
    margin-right: 15px;
}

.wrapper .main-wrapper .right-wrapper .news-content {
    word-wrap: normal;
    padding-right: 10px;
    font-size: 14px;
    color: #666666;
    line-height: 30px;
    margin-bottom: 80px;
    text-align: justify;
}
.news-content {
    word-wrap: normal;
    padding-right: 10px;
    font-size: 15px;
    color: #666666;
    line-height: 30px;
    margin-bottom: 80px;
    text-align: justify;
}
.wrapper .main-wrapper .right-wrapper .auth{
    padding: 0 0 0 700px
}
.auth{
    padding: 0 0 0 700px
}

.wrapper .main-wrapper .left-menu .title span.line-icon span.line.short-line {
    width: 50%;
}

.wrapper .main-wrapper .left-menu .title span.line-icon span.line.long-line {
    width: 100%;
    margin-top: 5px;
}

.wrapper .main-wrapper .left-menu ul.news-menu {
    padding-left: 20px;
    color: #999999;
    font-size: 14px;
    margin-top: 25px;
}

.wrapper .main-wrapper .left-menu ul.news-menu li.menu-item {
    border-radius: 2px;
    border: 1px solid #E5E5E5;
    margin-bottom: 15px;
    transition: all .5s;
    padding: 12px 20px;
    background-color: #ffffff;
    cursor: pointer;
    text-align: right;
}

.wrapper .main-wrapper .left-menu ul.news-menu li.menu-item.active {
    background-color: #ED1731;
    color: #ffffff;
}

.wrapper .main-wrapper .left-menu ul.recommend-list {
    width: 100%;
    font-size: 14px;
    color: #999999;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item {
    padding: 20px 0 25px;
    border-bottom: 1px solid #DBDBDB;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item.active {
    color: #ED1731;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item.active .date p.day {
    color: #ED1731;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item.active .content p.news-title {
    color: #ED1731;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .date {
    margin-right: 10px;
    text-align: center;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .date p.day {
    color: #E6E6E6;
    font-size: 59px;
    font-family: 'Impact';
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .date p.month {
    line-height: 25px;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .content {
    overflow: hidden;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .content p.news-title {
    color: #888888;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    line-height: 20px;
    margin-bottom: 20px;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .content .keyword {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.wrapper .main-wrapper .left-menu ul.recommend-list li.recommend-item .content .keyword span {
    padding: 5px 10px;
    font-size: 12px;
    margin-left: 15px;
    border: 1px solid #ECECEC;
    border-radius: 4px;
}

.wrapper .main-wrapper .right-wrappers {
    flex: 1;
    overflow: hidden;
    border-left: 1px solid #E5E5E5;
    padding: 0px 0 40px 15px;
    margin-bottom: 10px;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list {
    width: 100%;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item {
    width: 100%;
    padding: 20px 0 40px;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #E5E5E5;
    cursor: pointer;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item img {
    display: block;
    width: 140px;
    height: 120px;
    border-radius: 5px;
    box-shadow: 0 5px 15px 5px rgba(0, 0, 0, 0.1);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    margin-right: 15px;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .content {
    flex: 1;
    overflow: hidden;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .content p.title {
    color: #666666;
    font-size: 22px;
    line-height: 40px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item  p.title {
    color: #666666;
    font-size: 22px;
    line-height: 40px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .title {
    color: #666666;
    font-size: 22px;
    line-height: 40px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}
.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .title span {
    font-size: 14px;
    color: #999999;
    line-height: 28px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}


.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item  p.title span {
    font-size: 14px;
    color: #999999;
    line-height: 28px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}
.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item  p.detail {
    font-size: 14px;
    color: #999999;
    line-height: 20px;
}



.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .content p.date {
    font-size: 13px;
    color: #999999;
    line-height: 26px;
}

.wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .content p.detail {
    font-size: 14px;
    color: #999999;
    line-height: 28px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}

.wrapper .main-wrapper .right-wrappers .page-control {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 80px;
}

.wrapper .main-wrapper .right-wrappers .page-control button {
    display: inline-block;
    width: 40px;
    height: 40px;
    border-radius: 2px;
    background-color: #ffffff;
    background-position: center;
    background-repeat: no-repeat;
    border: 1px solid #E5E5E5;
    font-size: 14px;
    color: #877771;
    margin-right: 12px;
    cursor: pointer;
}

.wrapper .main-wrapper .right-wrappers .page-control button:hover {
    background-color: #E5E5E5;
    color: #877771;
}

.wrapper .main-wrapper .right-wrappers .page-control button.active {
    background-color: #ED1731;
    color: #ffffff;
}

.wrapper .main-wrapper .right-wrappers .page-control button:last-of-type {
    margin-right: 0;
}

.wrapper .main-wrapper .right-wrappers .page-control button.first-page {
    background-image: url("https://jjh-official.oss-cn-shenzhen.aliyuncs.com/pc/zqy@2x.png");
    background-size: 100% 100%;
    border: none;
}

.wrapper .main-wrapper .right-wrappers .page-control button.pre-page {
    background-image: url("https://jjh-official.oss-cn-shenzhen.aliyuncs.com/pc/pre@2x.png");
    background-size: 100% 100%;
    border: none;
}

.wrapper .main-wrapper .right-wrappers .page-control button.next-page {
    background-image: url("https://jjh-official.oss-cn-shenzhen.aliyuncs.com/pc/next@2x.png");
    background-size: 100% 100%;
    border: none;
}

.wrapper .main-wrapper .right-wrappers .page-control button.last-page {
    background-image: url("https://jjh-official.oss-cn-shenzhen.aliyuncs.com/pc/zhy@2x.png");
    background-size: 100% 100%;
    border: none;
}
