/*响应式css 小于767*/
@media screen and (max-width: 767px){

    .wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .title{
        color: #333333;
        font-size: 12px;
        line-height: 28px;
        font-weight: bold;
    }
    .wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item .title span{
        color: #999999;
        font-size: 5px;
        line-height: 24px;
    }
    .bg-title #imgs1 {
        /*overflow: hidden;*/
        width: 100%;
        height: 100px;
    }
    .pag .layui-laypage a {
        padding: 0 8px;
    }
    .pag .layui-laypage span {
        padding: 0 8px;
    }
    .wrapper .main-wrapper .right-wrapper .news-content {
        padding-right: 0px;
    }
    .wrapper .main-wrapper .right-wrapper .auth{
        padding: 0 0 0 0px
    }
    .auth{
        padding: 0 0 0 0px
    }
}
/*iphone5*/
@media screen and (min-width: 320px)and (max-width: 569px){

    .wrapper .main-wrapper .right-wrappers ul.news-main-list li.news-main-item img {
        width: 80px;
        height: 110px;
    }
    .wrapper .main-wrapper{
        width: 290px;
    }
}
@media screen and (min-width: 375px )and (max-width: 667px){
    .wrapper .main-wrapper{
        width: 345px;
    }
}
/*隐藏左边菜单*/
@media screen and (max-width: 1200px){
    /*隐藏左边菜单*/
    .wrapper .main-wrapper .left-menu{
        display: none !important;
    }
    /*pdc*/
}




